import React, { useState } from "react";
import styles from './LoginForm.module.css';
import lock from '../../../../assets/images/lock.svg';
import Button from './../../../common/Button/Button';
import google from '../../../../assets/images/google.svg';
import facebook from '../../../../assets/images/facebook.svg';
import yandex from '../../../../assets/images/yandex.svg';
import axios from "axios";
import { useNavigate } from "react-router-dom";

export default function LoginForm() {

  const [login, setLogin] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();
  const url = 'https://gateway.scan-interfax.ru/api/v1/account/login'

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post(url, { login: login, password: password });
      localStorage.setItem('token', JSON.stringify(response.data.accessToken));
      navigate('/search');
    } catch (error) {
      alert(error.response.data.message);
    }
  }

  return (
    <div>
      <img src={lock} alt='' className={styles.image} />
      <form className={styles.form} onSubmit={handleSubmit}>
        <div className={styles.container}>
          <div className={styles.linksContainer}>
            <button className={styles.active} disabled></button>
            <button className={styles.inactive} disabled></button>
          </div>
          <p className={styles.text}>   :</p>
          <input
            type='tel'
            required
            className={styles.input}
            placeholder="+7(___)___-__-__"
            value={login}
            onChange={(e) => setLogin(e.target.value)}
          />
          <p className={styles.text}>:</p>
          <input
            type='password'
            required
            className={styles.input}
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            autoComplete='new-password'
          />
          <div className={styles.submitButton}>
            <Button text='' />
          </div>
          <a className={styles.link}> </a>
          <p className={styles.text}> :</p>
          <div className={styles.social}>
            <button className={styles.button} disabled>
              <img src={google} alt='Google' />
            </button>
            <button className={styles.button} disabled>
              <img src={facebook} alt='Facebook' />
            </button>
            <button className={styles.button} disabled>
              <img src={yandex} alt='Yandex' />
            </button>
          </div>
        </div>
      </form>
    </div>
  )
}
